#main-header{
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 0.5em;
  min-height: var(--header-height);
  background-color: var(--current-weather-clr);
  z-index: 3;
  isolation: isolate;

}

.countries-search-holder{
  display: flex;
  margin: .7em 0;
  margin-top: 100px;
  margin-top: min( 160px, 30vw);

  height: 2em;
  width: clamp(300px ,70vw ,500px);
  max-width: 500px;
  justify-self: flex-end;
  align-self: center;
  order: 999;
}

.countries-search-bar-holder{
  --left-padding:.7em;
  position: relative;
  flex: 20;
  & label{
    position: absolute;
    left: var(--left-padding);; top: 0;bottom: 0;
    margin: auto 0;
    height: 1.1em;
    opacity: .7;
    cursor: text;
    text-transform: capitalize;
    pointer-events: none;
  }
}
  #countries-search-bar{
    appearance: none;
    cursor: text;
    width: 100%;height: 100%;
    padding: .2em var(--left-padding);


    font-size: 1em;
    border-radius: 0 5vw 5vw 0;
    outline: none;
    &:not(:placeholder-shown) + label ,
    &:focus + label     {
      opacity: 0;
    }
    &:focus ~ .autocomplete-countries-holder {
      display: block;
    }
  }

  .search-country-button{
    background-color: var(--comp-blue);
    color: white;
    border: 2px solid black ;
    cursor: pointer;
    border-radius: 10px 0 0 10px;
    border-right: none;
    &:hover{
    background-color: rgb(65, 160, 255);

    }
  }


  .autocomplete-countries-holder{
    display: none;
    width: calc(100% - 20px);
    height: fit-content;
    max-height: calc(40px * 6 );
    overflow-y: scroll;
    border-bottom: 5px solid var(--comp-blue);


    &:empty {
    border-bottom: none;

    }
  }
    .recommended-country{
      background-color: white;
      width: 100%;
      padding: .2em .5em;
      font-weight: 400;
      border: 1px solid black;

      transition: transform 200ms ease-in-out;
      transform: scale(1);
      transform-origin: left;

    }
    .recommended-country.selected{
      background-color: var(--comp-blue);
      color: white;
      transform: scale(1.2);
      & strong {
        color: rgb(235, 233, 233);
      }
    }

