.weather-header-parrent{
  position:absolute;inset:0 ; 
  overflow:hidden;
  z-index: -1;
  pointer-events: none;

  display: flex;

  &::before{
    content: " احوال الطقس";
    margin: auto;
    justify-self: center;align-self: center;
    font-size: 2em;
    color: white;
  }
}

.header-weather-inactive .header-weather {
  opacity: 0;
}
.weather-header-parrent:not(.header-weather-inactive){
  &::before{
    opacity: 0;
  }
}

 .header-weather{
  --box-size:80px;

  position: absolute;
  // border: 2px solid red;
  width: var(--box-size);
  height: var(--box-size);;

  background-image: url("../graphics/static-cloud.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size:  max( 100px , 180%  ) ;

  opacity: 1;
  z-index: -2;

  &.night{
    background-color: var(--comp-night)  !important;
  }
}

  .sunmoon{
    left: 30vw; top: 2vw;
    background-image: url("../graphics/clear-sun.svg");
    z-index: -2;

    transform:  scale( var(--icon-size) );
    

    &.moon{
      background-image: url("../graphics/clear-moon.svg");
    }
  }

  .cloud{
    --cloud-height:0px;
    --could-distance:40px;


    top: calc(2vw + var(--box-size) - (var(--box-size)/4 ) - var(--cloud-height));
    z-index: -1;

    animation: moveRight 4.5s ease-in-out ;

    animation-fill-mode: both;
    animation-direction: alternate;
    animation-iteration-count: infinite;

    &.rainy{
      background-image: url("../graphics/rainy.svg");
    }
    &.stormy{
      background-image: url("../graphics/thunder.svg");
    }
    &.snowy{
      background-image: url("../graphics/snowy.svg");
    }

    

  }

  .cloud1{
    left: 35vw;
    animation-duration: 8s;

    // animation: name duration timing-function delay iteration-count direction fill-mode;
  }
  .cloud2{
    --cloud-height:25px;
    --could-distance:150px;
    left: 60vw;

    animation-name:moveMiddle ;
    animation-duration: 20s;
    animation-direction: normal;


  }
  .cloud3{
    --cloud-height:10px;
    --could-distance:90px;
    right: 30vw;

    animation-name:  moveLeft;
    animation-duration: 6s;
  }
  .cloud4{
    --cloud-height:-15px;
    --cloud-distance :100px;
    left: 0;
    animation-name: moveLeft;
  }
  .cloud5{
    right: 0;
  }







  @keyframes moveLeft{
    from{
      transform: translateX(0) scale( var(--icon-size) );
    }
    to{
      transform: translateX( calc(var(--could-distance) * -1)) scale( var(--icon-size)  )  ;
    }
  }

  @keyframes moveMiddle{
    0%{
      transform: translateX(0) scale( var(--icon-size) );
    }
    25%{
      transform: translateX( calc( var( --could-distance) / 2 )) scale( var(--icon-size) );
    }
    75%{
      transform: translateX( calc( var( --could-distance) * -1)) scale( var(--icon-size) );

    }
    100%{
      transform: translateX(0) scale( var(--icon-size) );
    }
  }

  @keyframes moveRight{
    from{
      transform: translateX(0) scale( var(--icon-size) );
    }
    to{
      transform: translateX(var(--could-distance)) scale( var(--icon-size) );
    }
  }


  // @media(min-width:500px){
  //   .cloud{
  //     scale: 2;
  //   }
  // }

  @media(min-width:400px){
    :root{
      --icon-size:1.3  !important;

    }
  }