

#rest-of-page{
  position: relative;
  min-height: calc( 100vh - var(--header-height));
  background-color: var(--current-weather-clr);
  padding:  .6em ;

  &::after{
    content: "";
    position: absolute; inset: 0;

    background-color: white;
    opacity: .6;
    z-index: 1;
  }
}

.page-content{
  --time-selectors-holder-width:110px;

  position: relative;
  margin: 0 auto;
  margin-top: calc( var(--time-selectors-holder-width) );

  width: min( 100% , 1000px);
  min-width: fit-content;
  height: 800px;
  background-color: white;
  box-shadow: 0 0 5px 1px rgb(73, 73, 73);


  z-index: 2;
  animation: load .5s;
}
// elect time
  .select-time-holder{
    position: absolute;bottom: 100%;
    display: flex; gap: 5vw;
    padding: 0 7vw;
    width: 100%;
    overflow: hidden;
    height: var(--time-selectors-holder-width);


    & .time-selector-holder{
      background-color: rgb(34, 34, 34);
      clip-path: polygon(0 0, 50% 35%, 100% 0, 100% 100%, 0% 100%);

    
    & .daily-weather-selector , & .hourly-weather-selector{
        position: relative;
        padding: 2.5em 0;padding-bottom: 0;
        width: 80px;height: 100%;

        color: white;
        text-align: center;
        font-size: 1.2em;
        text-shadow: 0 2px 7px black;

        border-bottom:3px solid rgb(51, 51, 51) ;
      
        clip-path: polygon(0 0, 50% 35%, 100% 0, 100% 100%, 0% 100%);
        cursor: pointer;

        transition: opacity 100ms ease-in-out;
        transform: scale(.87);
        transform-origin: bottom center;
      }
  }

    & .daily-weather-selector{
      background-color: var(--daily-bgc);
      
    }

    & .hourly-weather-selector{
      background-color: var(--hourly-bgc);

    }

  }
  .daily-weather-selector:not(.selected) ,
  .hourly-weather-selector:not(.selected) {
    opacity: 0.7;
  }




    .daily-weather-holder , .hourly-weather-holder{
      width: 100%;
      height: 100%;

      transition: opacity 1s ease-in-out;
    }



    .daily-weather-holder.hiden , .hourly-weather-holder.hiden{
      opacity: 0;
    }

// daily weather
  .daily-weather-holder{
    padding: 10px 7px;
    display: flex; gap: 14px;
    flex-direction: column;
  }

  .selected-country-name{
    font-size: 2em;
  }


    .weather-info-grid{
      --weather-info-max-width:200px;
      padding: 8px;
      // width: 100%;
      width: fit-content;
      max-width: 100%;
      margin: 1em auto;
      border-radius: 10px;
      
      background-color: rgb(49, 49, 49);

      overflow: hidden;

      display: grid;
      grid-template-columns: 1fr 2fr ;
      grid-template-rows: minmax( 80px ,1fr) 2fr;
      grid-template-areas: 
              " cw da"
              " wi da" ;

      justify-items: right;
      justify-content: center;
      // gap: .5em;


    }
        .current-tempeture{

          padding-left: 10px;

          width: 100%; 
          max-width: var(--weather-info-max-width);


          font-size: 1.4em;
          text-align: left;

          // grid-column: 1;
          grid-area: cw ;

        }

        .daily-advice{
          position: relative;
          background-color: var(--comp-orange);
          color: white;
          padding:.6em ; padding-bottom: 2em;
          padding-bottom: 80px;
          width: 100%;
          // grid-column: -1;
          // grid-row: 1 / 3;

          grid-area: da ;

          & .actual-advice{
            display: block;
            padding: 1em .5em;
            font-family: 'Merriweather', serif;
            text-shadow: 1px 1px 5px rgb(54, 54, 54);
            background-color: rgba(65, 65, 65, 0.226);
          }
          & .daily-advice-origin{
            position: absolute;
            left: 16px;bottom: .3em;

            padding: .5em;
            width: fit-content;
            border-radius: 10px;

            color: white;
            background: linear-gradient(to top , hsl(33, 50%, 23%), hsl(33, 70%, 23%),transparent);



            text-shadow: 0 0 5px black;
            text-decoration: underline #5f4019;
            }

        }

    .weather-info{

      padding: 1em .3em ;
      width: min-content; 
      max-width: var(--weather-info-max-width);
      
      color: white;
      font-size: 80%;

      grid-area: wi ;

      & > * {
        width: max-content;
      }
    }
      // weekly weather
.weekly-weather-holder{
  display: flex;
  flex-direction: column;
  width: 100%;
  height: fit-content;

  border: 3px solid black;
}
.weekly-weather{
  background-color: var(--comp-gray-dark);

  border: 3px solid black;
  height: 60px;
  display: grid;
  grid-template-columns: 2fr 1fr 3fr;
  place-items: center;

  & .day-of-week{
    display: grid;place-items: center;
    width: 100%;height: 100%;
    font-size: 80%  ;
    color: var(--comp-orange);
    text-shadow: 0 0 5px black;
  }
  & .day-weather{
    height: 2.5em; 
    color: white;
    text-shadow: var(--apparent-txt-shadow);
    box-shadow:  -1px -1px 5px black;
    border: 2px solid black;
  }
  & .day-minmax-temperture{

    padding: .2em .5em;
    font-size: 80%;
    background-color: #fff;
    background-image: linear-gradient(to bottom right , rgba(0, 0, 0, 0.3) ,rgba(0, 0, 0, 0.2) , white 110%);


  }

  & .day-max-temperture{
    --temp-clr:red;
    height: 1.2em;
    padding: 0 .5em;
    position: relative;
    border-bottom:2px solid var(--temp-clr) ;
    margin-right: 2px;


  }
  & .hot-slash{
    display: none;
  }
  & .day-min-temperture{
    --temp-clr:blue;
    height: 1.2em;
    padding: 0 .5em;
    position: relative;
    border-bottom:2px solid var(--temp-clr) ;
    margin-left: 5px;
  }
}
    .day-min-temperture::before , .day-max-temperture::before {
      content: "";display: block;
      position: absolute; bottom: -2px;
      height: 100%;width: 2px;
      background-color: var(--temp-clr) ;
    }
        .day-min-temperture::before{
          right: 100%;
          transform: rotate(-30deg);
        }
        .day-max-temperture::before{
          left: 100%;
          transform: rotate(30deg);

        }






.temperture-with-icon{
  position: relative;
  isolation: isolate;
  background-color: white;
  background-image: url("../graphics/cloudy.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size:  min( 100px , 140%  ) ;

  color: white;
  text-shadow: var(--apparent-txt-shadow);

  border: 3px solid black;

 
  &.sunny{
    background-image: url("../graphics/clear-sun.svg");
  }
  &.moon{
    background-image: url("../graphics/clear-moon.svg");
  }


  &.cloudy{
    background-image: url("../graphics/cloudy.svg");
  }
  &.cloudy-sun{
    background-image: url("../graphics/cloudy-sun.svg");
  }
  &.cloudy-moon{
    background-image: url("../graphics/cloudy-moon.svg");
  }


  &.rainy{
    background-image: url("../graphics/rainy.svg");
  }
  &.stormy{
    background-image: url("../graphics/thunder.svg");
  }
  &.snowy{
    background-image: url("../graphics/snowy.svg");
  }
}

.temperture-with-icon::before{
  content: "";
  position: absolute;inset: 0;
  z-index: -1;
  background: radial-gradient(circle at bottom right , rgba(83, 83, 83, 0.2) 20%, rgba(83, 83, 83, 0.2) 60%  ,black );
  // background-color: red;
}

//  hourly weather

.hourly-weather-holder{
  position: absolute; inset: 0; bottom: unset;
  height: fit-content;
  padding: 16px;
  display: flex;
}
.page-content.big{
  --height:800px;
  height: var(--height);
}

    .morning-weather , .night-weather{
      flex-basis: 100%;
      display: flex;
      flex-direction: column;

      border: 3px solid black;

      box-shadow: 0 0 5px 4px rgb(187, 187, 187);
      & .hour{
        background-color: rgb(19, 19, 19);
        color: white;
        text-shadow: var(--apparent-txt-shadow);
        text-align: center;
      }

      & .chance-of-rain{

        justify-self: center;
        width: 90%;
        padding: .2em .5em;
        font-size: 80%;
        background-color: rgb(61, 61, 61);


        color: #ffffff;

        text-shadow: 0 0 5px black;
        border-radius: 7px;
      }

      & .temperture-with-icon{
        min-width: 80px ;

        background-position: 220%;
        background-size: 80%;
          &::before{
              background: radial-gradient(circle at bottom right ,
               rgba(83, 83, 83, 0.2) ,
                rgba(83, 83, 83, 0.2)   ,
                rgb(65, 65, 65) 70% );
          }
      }
    }
    .morning-weather{
      background-color: var(--comp-orange);
      & .hour{
        background-color: rgba(19, 19, 19, 0.747);
      }
      
    }
    .night-weather{
      background-color: var(--comp-night);
    }

  .hourly-weather{
    padding: 8px 12px;
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    justify-content: center;
    align-items: center;

    border: 2px solid rgb(51, 51, 51);
  }













@media(max-width:380px){
  .page-content{
    height: 900px;
  }
  .weather-info{
    width: min-content; 
  }
  .day-minmax-temperture{
    display: flex;
    flex-direction: column;
  }
}

@media(max-width:680px){
  .hourly-weather-holder{
    flex-direction: column;
  }
  .chance-of-rain{
    font-size: 75%;
  }



}

@media  (min-width:500px){
  .day-of-week{
    font-size: 1.2em !important;
  }
}
