@import "https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@1,300&family=Work+Sans:ital,wght@0,400;0,700;1,400&display=swap";
#main-header {
  min-height: var(--header-height);
  background-color: var(--current-weather-clr);
  z-index: 3;
  isolation: isolate;
  flex-direction: column;
  padding: .5em;
  display: flex;
  position: relative;
}

.countries-search-holder {
  height: 2em;
  width: clamp(300px, 70vw, 500px);
  max-width: 500px;
  order: 999;
