  margin: min(160px, 30vw) 0 .7em;
  display: flex;
}

.countries-search-bar-holder {
  --left-padding: .7em;
  flex: 20;
  position: relative;
}

.countries-search-bar-holder label {
  left: var(--left-padding);
  height: 1.1em;
  opacity: .7;
  cursor: text;
  text-transform: capitalize;
  pointer-events: none;
  margin: auto 0;
  position: absolute;
  top: 0;
  bottom: 0;
}

#countries-search-bar {
  appearance: none;
  cursor: text;
  width: 100%;
  height: 100%;
  padding: .2em var(--left-padding);
  border-radius: 0 5vw 5vw 0;
