  font-size: 1em;
}

#countries-search-bar:not(:placeholder-shown) + label, #countries-search-bar:focus + label {
  opacity: 0;
}

#countries-search-bar:focus ~ .autocomplete-countries-holder {
  display: block;
}

.search-country-button {
  background-color: var(--comp-blue);
  color: #fff;
  cursor: pointer;
  border: 2px solid #000;
  border-right: none;
  border-radius: 10px 0 0 10px;
}

.search-country-button:hover {
  background-color: #41a0ff;
}

.autocomplete-countries-holder {
  width: calc(100% - 20px);
  height: -moz-fit-content;
  height: fit-content;
  max-height: 240px;
  border-bottom: 5px solid var(--comp-blue);
  display: none;
  overflow-y: scroll;
}

.autocomplete-countries-holder:empty {
