}

.select-time-holder .time-selector-holder .daily-weather-selector, .select-time-holder .time-selector-holder .hourly-weather-selector {
  width: 80px;
  height: 100%;
  color: #fff;
  text-align: center;
  text-shadow: 0 2px 7px #000;
  clip-path: polygon(0 0, 50% 35%, 100% 0, 100% 100%, 0% 100%);
  cursor: pointer;
  transform-origin: bottom;
