}

.daily-advice .daily-advice-origin {
  width: -moz-fit-content;
  width: fit-content;
  color: #fff;
  text-shadow: 0 0 5px #000;
  background: linear-gradient(to top, #583e1d, #643f12, #0000);
  border-radius: 10px;
  padding: .5em;
