}

.weekly-weather-holder {
  width: 100%;
  height: -moz-fit-content;
  height: fit-content;
  border: 3px solid #000;
  flex-direction: column;
  display: flex;
}

.weekly-weather {
  background-color: var(--comp-gray-dark);
  height: 60px;
