  margin-right: 2px;
  padding: 0 .5em;
  position: relative;
}

.weekly-weather .hot-slash {
  display: none;
}

.weekly-weather .day-min-temperture {
  --temp-clr: blue;
  height: 1.2em;
  border-bottom: 2px solid var(--temp-clr);
  margin-left: 5px;
  padding: 0 .5em;
  position: relative;
}

.day-min-temperture:before, .day-max-temperture:before {
  content: "";
  height: 100%;
  width: 2px;
  background-color: var(--temp-clr);
  display: block;
  position: absolute;
  bottom: -2px;
}

.day-min-temperture:before {
  right: 100%;
