}

.day-max-temperture:before {
  left: 100%;
  transform: rotate(30deg);
}

.temperture-with-icon {
  isolation: isolate;
  color: #fff;
  text-shadow: var(--apparent-txt-shadow);
  background-color: #fff;
  background-image: url("cloudy.97ae0846.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: min(100px, 140%);
  border: 3px solid #000;
  position: relative;
}

.temperture-with-icon.sunny {
  background-image: url("clear-sun.e6432f77.svg");
}

.temperture-with-icon.moon {
