}

.temperture-with-icon:before {
  content: "";
  z-index: -1;
  background: radial-gradient(circle at 100% 100%, #53535333 20% 60%, #000);
  position: absolute;
  inset: 0;
}

.hourly-weather-holder {
  inset: 0;
  bottom: unset;
  height: -moz-fit-content;
  height: fit-content;
  padding: 16px;
  display: flex;
  position: absolute;
}

.page-content.big {
  --height: 800px;
  height: var(--height);
}

.morning-weather, .night-weather {
  border: 3px solid #000;
  flex-direction: column;
  flex-basis: 100%;
  display: flex;
  box-shadow: 0 0 5px 4px #bbb;
