#main-footer {
  min-height: 100px;
  color: #fff;
  background-color: #202020;
  padding: .5em 1em;
}

.weather-header-parrent {
  z-index: -1;
  pointer-events: none;
  display: flex;
  position: absolute;
  inset: 0;
