  margin: auto;
  font-size: 2em;
}

.header-weather-inactive .header-weather, .weather-header-parrent:not(.header-weather-inactive):before {
  opacity: 0;
}

.header-weather {
  --box-size: 80px;
  width: var(--box-size);
  height: var(--box-size);
  opacity: 1;
  z-index: -2;
  background-image: url("static-cloud.83b528e1.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: max(100px, 180%);
  position: absolute;
}

.header-weather.night {
  background-color: var(--comp-night) !important;
}

.sunmoon {
  z-index: -2;
  transform: scale(var(--icon-size));
  background-image: url("clear-sun.e6432f77.svg");
  top: 2vw;
  left: 30vw;
}

.sunmoon.moon {
  background-image: url("clear-moon.4985fbd5.svg");
}

.cloud {
  --cloud-height: 0px;
  --could-distance: 40px;
  top: calc(2vw + var(--box-size)  - var(--box-size) / 4 - var(--cloud-height));
  z-index: -1;
  animation: moveRight 4.5s ease-in-out infinite alternate both;
}

.cloud.rainy {
  background-image: url("rainy.c70f4a99.svg");
}

.cloud.stormy {
  background-image: url("thunder.ca1d0911.svg");
}

.cloud.snowy {
  background-image: url("snowy.b31fa220.svg");
}

.cloud1 {
  animation-duration: 8s;
  left: 35vw;
}

.cloud2 {
  --cloud-height: 25px;
  --could-distance: 150px;
  animation-name: moveMiddle;
  animation-duration: 20s;
  animation-direction: normal;
  left: 60vw;
}

.cloud3 {
  --cloud-height: 10px;
  --could-distance: 90px;
  animation-name: moveLeft;
  animation-duration: 6s;
  right: 30vw;
}

.cloud4 {
  --cloud-height: -15px;
  --cloud-distance: 100px;
  animation-name: moveLeft;
  left: 0;
}

.cloud5 {
  right: 0;
}

@keyframes moveLeft {
  from {
    transform: translateX(0) scale(var(--icon-size));
  }

  to {
    transform: translateX(calc(var(--could-distance) * -1) ) scale(var(--icon-size));
  }
}

@keyframes moveMiddle {
  0% {
    transform: translateX(0) scale(var(--icon-size));
  }

  25% {
    transform: translateX(calc(var(--could-distance) / 2) ) scale(var(--icon-size));
  }

  75% {
    transform: translateX(calc(var(--could-distance) * -1) ) scale(var(--icon-size));
  }

  100% {
    transform: translateX(0) scale(var(--icon-size));
  }
}

@keyframes moveRight {
  from {
    transform: translateX(0) scale(var(--icon-size));
  }

  to {
    transform: translateX(var(--could-distance)) scale(var(--icon-size));
  }
}

@media (min-width: 400px) {
  :root {
    --icon-size: 1.3 !important;
