@use "components/all";
@import url('https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@1,300&family=Work+Sans:ital,wght@0,400;0,700;1,400&display=swap');
:root{

  --current-weather-clr:#42C3FF;

  --main-clr:#42C3FF;
  --comp-blue:#317EE8;
  --comp-orange:#FFA942;
  --comp-gray-dark:#313131;
  --comp-night:#1d4db3;
  --clear:#42C3FF;
  --rainy:#1D83B3;
  --rainy2:#476E80;
  --snowy:#8FDBFF;
  --sunny:#FFCE36;
  --header-height:15vh;

  --daily-bgc :var(--current-weather-clr);
  --hourly-bgc:hsl(352, 100%, 68%) ;


  --font-outsider:'Merriweather', serif;
  --font-our :  'Work Sans', sans-serif;

  --apparent-txt-shadow:0 0 5px black;
  --temerture-unit:" °C";

  --could-distance:100px;
  --icon-size:1;


}

* ,*::after,*::before{
  box-sizing: border-box;
}
p,h1,h2{
  margin: 0;
}
body{

  margin: 0;padding: 0;
  text-transform:capitalize;
  font-family: 'Work Sans', sans-serif;
}

.block{
  width: 200px;height: 70px;
  border: 3px solid red;
  background: radial-gradient(circle at bottom right , rgba(0, 0, 0, 0.219) 20%, rgba(0, 0, 0, 0.555) 70%  ,black );
}
.hide{
  display: none;
}
// animations

@keyframes load {
  from{
    opacity: 0;
    transform:translateY(-30px) ;
  }
    
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

// info elements

  // when empty
.current-wind-speed:empty ,
.humidity:empty ,
.feels-like:empty{

  &::after{
    content: "" ;
  }

}


.current-wind-speed::before ,
.humidity::before , 
.feels-like::before{


    display: inline-block;
    width: 24px;
  
}

.temperture::after {
  content: var(--temerture-unit);
}
.humidity::before{
 content: "\f773° ";
 font-family: "Font Awesome 6 Free";
  color:var(--rainy);
  font-weight:900;

}

.humidity::after{
  content: " %";
}
.chance-of-rain{
  &::after{
    content: "%";
  }

  &::before{
    display: inline-block;
    width: 24px;
    height: 1.2em;
    background-position: center;
    background-repeat: no-repeat;
    background-size:  100% ;
    content: "";
    background-image: url("../graphics/rainy.svg");

  }
}



.feels-like::before{
  content: "\f185 ";
  font-family: "Font Awesome 5 Free";
  color: var(--sunny);


}

.current-wind-speed::after{
  content: " Km/h";
}

.current-wind-speed::before{
  content: "\f72e ";
   font-family: "Font Awesome 5 Free";
   color: rgb(142, 214, 75);
   font-weight:900;

}

